<template>
  <div class="contain">
    <h1 class="products">단어 연습</h1>
    <div class="wordheader">
      <a href="/wordcategory"><i class="fas fa-arrow-circle-left fa-3x">낱말연습</i></a>
      <!-- <div>현재문제(지울예정):{{this.idx+1}}</div> -->
      <div>{{questionsno}}단계</div>
    </div>
    <div class="maincontent">
      <div>
        {{index+1}}/{{totalword}}
      </div>
      <div class="goallist">
        <div v-for="(x) in 10" :key="x">
          <template v-if="index == x-1">
            <img src="../assets/images/tur.png" class="goalimg"/>
          </template>
          <template v-if="index != x-1 && x != 10">
            <img src="../assets/images/backgroundnull.png" class="goalimg" />
          </template>
          <template v-if="index!= 9 && x == 10">
            <img src="../assets/images/flag.png" class="goalimg" />
          </template>
        </div>
      </div>
      <div class="center">    
      </div>
      <div class="conmain">
        <div>
          <img src="http://cdn.kormedi.com/wp-content/uploads/2019/08/gettyimages-923832604_580.jpg" class="cimg" @click="clickread"/>
          <!-- 배포시 -->
          <!-- <img :src="url+nowshow.wordImg" class="cimg" @click="clickread"/> -->
          
          <div class="cfont"  @click="clickread" > 
            {{nowshow.word}}
            <!-- <button id="btn-read" @click="clickread">읽기</button> -->

            <!-- 스피커아이콘 -->
            <!-- <img src="../assets/images/read.png" class="speakimg"/> -->
          </div>
        </div>
        <div class="noimg"> 
          <button id="clear-ans" @click="clearCanvas" class="btn btn-info"><i class="fas fa-eraser"></i></button>
       
          <div class="inputdiv radiusboard">
            <div id="keyboard">
              <label>따라치기 : </label>
              <b-form-input v-model="anstext" type="text"/>
              <div class="rewrite">
              {{anstext}}
              </div>
            </div>
            <div id="penandmouse">
              <!-- <label for="">따라적기 : </label> -->
              <canvas ref="c" id="c" style="border: 1px solid rgb(170, 170, 170);  touch-action: none; user-select: none;" class="lower-canvas"></canvas>
              <div id="resultko">{{result}}</div>
            </div>
          </div>
          <div>
            <!-- <div class="floatleft"> -->
            <div class="clickbtns">
              <!-- <div id="iconkeyboard" > -->
              <!-- <img class="iconkeyboard radiusboard" src="../assets/images/keyboard.png" @click="keyboardInput" /> -->
              
              <v-btn @click="keyboardInput" id="iconkeyboard" class="clickbtn"><i class="fas fa-keyboard"></i>키보드</v-btn>
            <!-- </div> -->
              <!-- <div> -->
              <!-- <img id="iconpenandmouse" class="iconpenandmouse radiusboard" src="../assets/images/penandmouse.png" @click="touchInput" /> -->
              <v-btn @click="touchInput" id="iconpenandmouse" class="clickbtn"><i class="fas fa-pencil-alt"></i>펜/<i class="mdi mdi-mouse"></i>마우스</v-btn>
              <!-- </div> -->
            </div>
          </div>
        </div>
      </div>
    <div class="wordtestfooter">  
           
      <template v-if="index < totalword-1">
          <b-button @click="nextword">
              다음
          </b-button>
      </template>
      <template v-if="index >= totalword-1">
          <b-button @click="nextword">
              완료
          </b-button>
      </template>
    </div>
  </div>
  <img id="ansimgid" :src="ansimg">
  </div>
</template>

<script>
import axios from "axios";
import {fabric} from 'fabric';

export default {
    name: 'WordTest',
    props:{
        categoryNo:{
            type:String,
            default:""
        }
    },
    data() {
        return {
          API_ENDPOINT : 'https://inputtools.google.com/request?ime=handwriting&app=autodraw&dbg=1&cs=1&oe=UTF-8',
			
            //vue 생성할때 한번 받음
            // cateNo : "",
            // 캠버스 셋팅
            canvas: null,
            url: "http://k3d206.p.ssafy.io",
            anstext: "",
            ansimg:"",
            // 테스트 워드 수
            totalword:4,
            nowshow:[],
            // ====post 보낼 인자
            studentno:1,
            result:"",
            stencils: [],
            suggestions:[],
            preX: 0,
            preY: 0,
            curX:  0,
            curY : 0,
            pressedAt: 0,
            isPressing: false,
            maxX: 100000,
            minX: 0,
            maxY: 100000,
            minY: 0,
            shapes: [],
            // curShape:[],
            inputtype:0,

            index:0,
            show: true,
            wordlist:[],
            ttsList: [
              {
                testno: 5,
                wordimg: "http://cdn.kormedi.com/wp-content/uploads/2019/08/gettyimages-923832604_580.jpg",
                word: "사과",
                homeworkno:0,
                categoryno:1,
              },
              {
                testno: 6,
                wordimg: "https://cdn.vuetifyjs.com/images/cards/desert.jpg",
                word: "배",
                homeworkno:0,
                categoryno:1,           
              },
              {
                testno: 7,
                wordimg: "http://mimg.segye.com/content/image/2017/08/30/20170830515039.jpg",
                word: "포도",
                homeworkno:0,
                categoryno:1,
              },
              {
                testno: 4,
                wordimg: "https://i2.wp.com/sharehows.com/wp-content/uploads/2014/10/%EA%B0%80%EC%9D%84%EC%9D%84-%EB%8C%80%ED%91%9C%ED%95%98%EB%8A%94-%EA%B0%90-4.jpg?resize=800%2C533",
                word: "감",
                homeworkno:0,
                categoryno:1,
              },
            ]
        };
    },
    mounted(){
      // this.loadStencils();
        if(document.readyState == 'complete'){
            prepareCanvas();
            this.canvas = window.__canvas;
            this.setEventHandlers();
        }else{
            window.onload = () => {
                prepareCanvas();
                this.canvas = window.__canvas
                this.setEventHandlers();
            } 
        }
        
        function prepareCanvas() {   
            var canvas = window.__canvas = new fabric.Canvas('c', {
                isDrawingMode: true
            });
            // console.log(canvas);
            // if(window.innerWidth>800)
              canvas.setWidth(window.innerWidth*3.5/10);
              canvas.setHeight(window.innerWidth*3.5/10);
        // console.log("!!!");
            fabric.Object.prototype.transparentCorners = false;

            var canvases = window.__canvases || window.canvases;

            canvas && canvas.calcOffset && canvas.calcOffset();

            if (canvases && canvases.length) {
                for (var i = 0, len = canvases.length; i < len; i++) {
                    canvases[i].calcOffset();
                }
            }
        }        
        //배포시
        this.wordlist = this.ttsList;
        ///////////
        this.nowshow = this.wordlist[0];  
        this.touchInput();
        this.createCanvas();
          
    },
    methods:{
      getNoni(){
			axios.post(this.API_ENDPOINT, {
				input_type : 0,
				requests: [{
					ink:  this.ansimg,
					language : 'ko',
					writing_guide: {
						"width":this.canvas.width,
						"height":this.canvas.height
					}
				}]
			})
			.then(({data}) =>{
				console.dir(data);
				// var result=data;
				// document.getElementById("result").innerText=result;
			})
		},
      setEventHandlers(){
        this.canvas.on('mouse:down',this.onDrawingMouseDown)
        this.canvas.on('mouse:move',this.onDrawingMouseMove)
        this.canvas.on('mouse:up',this.onDrawingMouseUp)
      },
      createCanvas(){
        var upper = document.getElementsByClassName('upper-canvas');
        console.log(upper);
        // upper.style.height = '10vh';
      },
      keyboardInput(){
        var keyboard = document.getElementById('keyboard');
        keyboard.style.display = 'block';
        
        var iconkeyboard = document.getElementById('iconkeyboard');
        iconkeyboard.style.opacity = '1';

        var touch = document.getElementById('penandmouse');
        touch.style.display= 'none';
        
        var icontouch = document.getElementById('iconpenandmouse');
        icontouch.style.opacity= '0.3';
        var iclear = document.getElementById('clear-ans');
        iclear.style.opacity= '0';
        iclear.style.margin='30vw';
        this.inputtype=1;
      },
      touchInput(){
        var keyboard = document.getElementById('keyboard');
        keyboard.style.display = 'none';
        var iconkeyboard = document.getElementById('iconkeyboard');
        iconkeyboard.style.opacity = '0.3';
        
        var touch = document.getElementById('penandmouse');
        touch.style.display= 'block';
        
        var icontouch = document.getElementById('iconpenandmouse');
        icontouch.style.opacity= '1';
        var iclear = document.getElementById('clear-ans');
        iclear.style.opacity= '1';
        iclear.style.margin='1vw';
        
        this.inputtype=0;
      },
      clearCanvas(){
        // this.anstext ="";
        this.canvas.clear();
        this.shapes = [];
      },
      getWord(){
        // console.log(this.cateNo);
        // console.log(this.categoryNo);
        axios.get('/wordtest/categorywordtest?categoryNo='+this.categoryNo)
        .then(res=>{
          this.wordlist = res.data;
          this.totalword = this.wordlist.length;
          this.nowshow = this.wordlist[0];
          console.log(res.data)
        })
        .catch(err =>{
          console.log(err)
        })
      },
      nextword(){
        this.index = this.index+1;
        console.log(this.index);
        var c = document.getElementById('c');
        this.ansimg = c.toDataURL();
        console.log(this.canvas);

      //배포시
        // var data = {
        //     'wordTestNo' : this.nowshow.testNo,
        //     'studentNo' : this.studentno,
        //     'submitAnswer' : this.anstext,
        //     'submitImg' : this.ansimg,
        // }
        // console.log(data);
        // var config={
        //   header:{
        //     'Content-Type' : 'application/json',
        //   }
        // };

        // axios.post('/wordtestsubmit/wordtestsubmit',data,config)
        //   .then(
        //     res =>{
        //       res
        //       this.$store.state.postans =1
        //       let configimg ={
        //         header : {
        //           'Content-Type' : 'multipart/form-data',
        //           // 'Authorization' : 'JWT' + localStorage.getItem("token")
        //         }
        //       };
        //       console.log(res);
        //       let form = new FormData();
        //       form.append("file",this.ansimg);
        //       form.append("testNo",this.studentno);
        //       axios.put('/wordtestsubmit/imgword',form,configimg) //이미지업데이트
        //       .then((data) =>{
        //         console.log(data);
        //         this.intervalid = setInterval(()=>{
        //         this.$store.state.loadingflag = false;
        //         location.reload()
        //         }, 1000);
        //       })
        //       .catch((error)=>{
        //         console.log(error);
        //       })
        //     }
        //   )
        // .catch(err => {
        //     err
        // });

    ////////
        //word_test_no 는 testno
        // studentno:0,
        // submit_answer:"",
        // submit_img:"",
        this.getNoni();
        this.nowshow = this.wordlist[this.index];
        this.clearCanvas();
        
      },
      onDrawingMouseDown(e){
            this.isPressing = true;
            
            if(e.pointer.x < 0){
                e.pointer.x = 0;
            }
            if(e.pointer.y < 0){
                e.pointer.y = 0;
            }
            
            console.dir(e)

            // console.log('down')
        },
        onDrawingMouseMove(e){
            if(!this.isPressing) return;
            if(e.pointer.x < 0){
                e.pointer.x = 0;
            }
            if(e.pointer.y < 0){
                e.pointer.y = 0;
            }
            
            // console.log('move')

            (e.pointer.x > this.maxX) && (this.maxX = e.pointer.x);
            (e.pointer.x < this.minX) && (this.minX = e.pointer.x);
            (e.pointer.y > this.maxY) && (this.maxY = e.pointer.y);
            (e.pointer.y < this.minY) && (this.minY = e.pointer.y);
        },
        onDrawingMouseUp(){
            this.isPressing = false;
            this.shapes.push(this.curShape);
        },
      clickread(){
        if (typeof SpeechSynthesisUtterance === "undefined" || typeof window.speechSynthesis === "undefined") {
          alert("이 브라우저는 음성 합성을 지원하지 않습니다.")
          return
        }
        
        window.speechSynthesis.cancel() // 현재 읽고있다면 초기화

        const speechMsg = new SpeechSynthesisUtterance()
        speechMsg.rate =  1; // 속도: 0.1 ~ 10      
        speechMsg.pitch = 1; // 음높이: 0 ~ 2
        speechMsg.lang = "ko-KR";
        speechMsg.text = this.nowshow.word;
        
        // SpeechSynthesisUtterance에 저장된 내용을 바탕으로 음성합성 실행
        window.speechSynthesis.speak(speechMsg)
      },
    },
    created(){
      // 배포시
        // this.getWord();
        ////
    },
    // watch: {
    //   userlimit: function() {
    //   this.userFail(this.userlimit);
    //   },
    // },
    components: {
        // TtsTest
        }
    }
</script>


<style scoped>
.goallist{
  display: flex;
  justify-content: center;
}
.goalimg{
  width: 9vw;
  height: 9vw;
}

.clickbtns {
    height: 60px;
    margin: 26px 0;
    display: center;
}
.clickbtn {
    border: 3px solid blue;
    background-color: rgb(119, 197, 17);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 5px 0px 0px;
    margin-right: 16px;
}

.wordheader{
  display: flex;
  margin: 0;
  justify-content: space-between;
  font-size: 2vh;
}
.wordheader>div{
  font-size:4vh;
}


.wordtestfooter{
  margin:10px;
}
.iconkeyboard{
  width: 100px;
  height: 100px;
}
.iconpenandmouse{
  width: 200px;
  height: 100px;
}
.radiusboard{
  
  border:solid 1px;
  border-color:rgb(200, 200, 200);
  border-radius: 5px;
}

.speakimg{
    width: 30px;
    display: inline-block;
}
.cfont{
    font-size:8vh;
}
.cimg{
    width: 35vw;
    height: 50vh;
    margin: auto 10px;
    border-radius: 10%;
  }
.floatleft{
  /* float:left; */
  display:flex;
}


  .inputdiv{
  width: 35vw;
  background-color: skyblue;
  height: 35vw;
  }
  .maincontent{
    margin: 0 auto;
  }
  .rewrite{
    line-height: 25vw;
    font-size: 80px;
  }
@media (max-width: 480px) {
  
.floatleft{
  /* float:left; */
  display:inline-flex;
}
  .cimg{
    width: 90vw;
    margin: 5px auto;
    height: 30vh;
    border-radius: 10%;
  }
.conmain{
  display: block;
}
.cfont{
    font-size:6vh;
}
.iconkeyboard{
  width: 15vw;
  height: 5vh;
}
.iconpenandmouse{
  width: 30vw;
  height: 5vh;
}
.products{
  display: none;
}
.inputdiv{
  width:95vw;
  margin: 0px auto 5px auto;
  
}
}
@media (min-width:481px) and (max-width: 760px){
  
.contain{
  margin:2vh 2vw;
}
.products{
  display: none;
}
  .iconkeyboard{
  width: 50px;
  height: 50px;
}
.iconpenandmouse{
  width: 100px;
  height: 50px;
}
  .cimg{
    width: 30vw;
    height: 50vh;
    margin: auto 10px;
    border-radius: 10%;
    max-height: 230px;
  }
  
}

@media (min-width:761px) and (max-width: 1370px){
  .conmain{
    display: flex;
  justify-content: center;
  }
  .maincontent{
    margin: 0 auto;
  }
.contain{
  margin:2vh 2vw;
}
.products{
  display: none;
}
  .iconkeyboard{
  width: 50px;
  height: 50px;
}
.iconpenandmouse{
  width: 100px;
  height: 50px;
}
  .cimg{
    width: 35vw;
    height: 35vw;
    margin: auto 10px;
    border-radius: 10%;
  }
}

@media (min-width:1371px) and (max-width: 2360px){
  .conmain{
    display: flex;
  justify-content: center;
  }
  
.contain{
  margin: 0px auto;
}
  .iconkeyboard{
  width: 50px;
  height: 50px;
}
.iconpenandmouse{
  width: 100px;
  height: 50px;
}
  .cimg{
    width: 35vw;
    height: 35vw;
    margin: auto 10px;
    border-radius: 10%;
  }
}

#clear-ans{
  position: absolute;
  margin: 1vw;
  display: block;
  z-index: 1;
}
</style>
